<template>
  <div id="cesiumContainer1"></div>
</template>

<script>
import * as Cesium from "cesium/Cesium";
import widget from "cesium/Widgets/widgets.css";
var viewer = null;
let position = null;
let entity = null;
var start;
var stop;
export default {
  name: "drawLine",
  props: {},
  data() {
    return {
      tileset: null,
    };
  },
  computed: {},
  watch: {},
  components: {},
  created() {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
      Cesium.Ion.defaultAccessToken =
        "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJqdGkiOiIzZjFjYmZmNS1hYmNjLTRhZWUtYjlkNi02ODVmOGRjNGQ2N2MiLCJpZCI6Mzg4MzUsImlhdCI6MTYwNjg3Mzk0MX0.R0iO5eELEnpRqQCzoa33UZakcsTYUidaTP9nLa342wY";
      viewer = new Cesium.Viewer("cesiumContainer1", {
        infoBox: false, //Disable InfoBox widget
        selectionIndicator: false, //Disable selection indicator
        shouldAnimate: true, // Enable animations
        terrainProvider: Cesium.createWorldTerrain(),
      });

      viewer._cesiumWidget._creditContainer.style.display = "none"; // 隐藏版权

      this.startDraw();
    },
    startDraw() {
      //Enable lighting based on the sun position
      viewer.scene.globe.enableLighting = true;

      //Enable depth testing so things behind the terrain disappear.
      viewer.scene.globe.depthTestAgainstTerrain = true;

      //Set the random number seed for consistent results.
      Cesium.Math.setRandomNumberSeed(3);

      //Set bounds of our simulation time
      start = Cesium.JulianDate.fromDate(new Date(2015, 2, 25, 16));
      stop = Cesium.JulianDate.addSeconds(start, 360, new Cesium.JulianDate());

      //Make sure viewer is at the desired time.
      viewer.clock.startTime = start.clone();
      viewer.clock.stopTime = stop.clone();
      viewer.clock.currentTime = start.clone();
      viewer.clock.clockRange = Cesium.ClockRange.LOOP_STOP; //Loop at the end
      viewer.clock.multiplier = 10;

      //Set timeline to simulation bounds
      viewer.timeline.zoomTo(start, stop);

      //Generate a random circular pattern with varying heights.

      //Compute the entity position property.
      position = this.computeCirclularFlight(-112.110693, 36.0994841, 0.03);
      //Actually create the entity
      entity = viewer.entities.add({
        //Set the entity availability to the same interval as the simulation time.
        availability: new Cesium.TimeIntervalCollection([
          new Cesium.TimeInterval({
            start: start,
            stop: stop,
          }),
        ]),

        //Use our computed positions
        position: position,

        //Automatically compute orientation based on position movement.
        orientation: new Cesium.VelocityOrientationProperty(position),

        //Load the Cesium plane model to represent the entity
        model: {
          uri: "../SampleData/models/CesiumAir/Cesium_Air.glb",
          minimumPixelSize: 64,
        },

        // Show the path as a pink line sampled in 1 second increments.
        path: {
          resolution: 1,
          material: new Cesium.PolylineGlowMaterialProperty({
            glowPower: 0.1,
            color: Cesium.Color.YELLOW,
          }),
          width: 10,
        },
      });

      /* 把位置数据进行插值计算   让模型绕多边形轨迹移动 */
      // entity.position.setInterpolationOptions({
      //   interpolationDegree: 1,
      //   interpolationAlgorithm: Cesium.LinearApproximation,
      // });

      /* 绕圆形轨迹飞行 */
         entity.position.setInterpolationOptions({
            interpolationDegree: 5,
            interpolationAlgorithm:
              Cesium.LagrangePolynomialApproximation,
          });
       con
      

      /* 垂直向下看视角 */
        viewer.zoomTo(
          viewer.entities,
          new Cesium.HeadingPitchRange(0, Cesium.Math.toRadians(-90))
        );

    //   viewer.zoomTo(
    //     viewer.entities,
    //     new Cesium.HeadingPitchRange(
    //       Cesium.Math.toRadians(-90),
    //       Cesium.Math.toRadians(-15),
    //       7500
    //     )
    //   );
    },
    /* 创建几个轨迹位置点   再通过entity.position.setInterpolationOptions进行插值计算 */
    computeCirclularFlight(lon, lat, radius) {
      var property = new Cesium.SampledPositionProperty();
      for (var i = 0; i <= 360; i += 45) {
        var radians = Cesium.Math.toRadians(i);
        var time = Cesium.JulianDate.addSeconds(
          start,
          i,
          new Cesium.JulianDate()
        );
        var position = Cesium.Cartesian3.fromDegrees(
          lon + Math.random() * radius, // radius * 1.5 * Math.cos(radians),
          lat + Math.random() * radius, //radius * Math.sin(radians),
          Cesium.Math.nextRandomNumber() * 500 + 1750
        );
        property.addSample(time, position);
        // Also create a point for each sample we generate.
        viewer.entities.add({
          position: position,
          point: {
            pixelSize: 8,
            color: Cesium.Color.TRANSPARENT,
            outlineColor: Cesium.Color.YELLOW,
            outlineWidth: 3,
          },
        });
      }
      return property;
    },
  },
};
</script>

<style scoped>
#cesiumContainer1 {
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
</style>
